
/* =============================== 섹션 이름  */
.basis-section{
  /* 비주얼 영역 */
  &.key_visual-section{  margin-top: 0; padding: 0; max-width: 1920px;
    .bgimg { height: 599px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
    .type-dday { background-image: url(/images/pc/auction/keyvisual_dday.jpg);}
  }

  /* 진행,예정 - 텝 */
  &.tab-auction_other-section {}
  
  /* 진행,예정 경매 - 리스트 */
  &.auction_other_list-section { margin-top: 0px; }
  @media all and (max-width:$G-wMobile)  {
    &.key_visual-section{
      .bgimg { width: 100%; height:0; padding-bottom: 111.1111%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
      .type-dday { background-image: url(/images/mobile/auction/keyvisual_dday.jpg);}
    }
    &.tab-auction_other-section  { padding: 0; }
    &.auction_other_list-section { margin-top: 0px; }
  }

  /* 경매결과 - 텝 */
  &.tab-auction_result-section {}
  
  /* 경매결과 - 리스트 */
  &.auction_result_list-section {
    margin-top: 0;
    @media all and (max-width:$G-wMobile)  {
      margin-top: 0;
    }
  }
  

}





/* =============================== 페널이름 */
.content-panel{
  /* =====진행,예정 경매 리스트===== */
  &.type_panel-auction_list { 
    @media all and (max-width:$G-wMobile)  {
    }
  }
  /* ===경매 리스트=== */
  .auction-list {
    li {
      .li-inner { padding: 40px 0; border-bottom: 1px solid #ccc; }
    }

    /* 리스트 아이템 */
    .item-article { display: flex; justify-content: space-between;
      .typo-area  { width: calc( 100% -280px); }
      .image-area { width: 280px; .img-align { background-color: #ededed;}}
      @media all and (max-width:$G-wMobile)  { display: block; justify-content:normal;
        .typo-area  { width: 100%; }
        .image-area { width: 100%; margin-top: 20px; }
      }

      .product_info {
        .state-box  {
          span.type-online { border: solid 1px #3e9ccc; color:#3e9ccc; }
          span.type-live   { border: solid 1px #10a68f; color:#10a68f; }
          span.type-d_day  { border: solid 1px #333; color:#fff; background-color: #333;}
          span { display: inline-block; padding: 0 10px;
            border: solid 1px #3e9ccc;
            font-size: 16px; font-weight: bold; letter-spacing: .4px; line-height: 19px; color:#3e9ccc;
          }
        } 
        .title-box  { margin-top: 10px;
          span { font-size: 26px; font-weight: 600; color:$colorB2; }
        }
        .info-box   { margin-top: 20px;
          dl { position: relative; padding-left: 65px;}
          dt, dd { font-size: 18px; line-height: 1.78;}
          dt { position: absolute; left: 0; padding-right: 10px; margin-right: 10px;
            &::before { content: ""; position: absolute; margin-top: 8px; right: 0;
              width: 1px; height: 14px; background-color: #ccc;
            }
            color:$colorB2;
            
          }
          dd {  color:$colorD; }
        }
        .btn-box    { margin-top: 20px;
          .btn { width: 170px;
            &:disabled {  color: $colorS; background-color: #e4e4e4; border-color: #e4e4e4; }
          } 
        }
      }
      @media all and (max-width:$G-wMobile)  {

        .product_info {
          .state-box  {
            span { display: inline-block; padding: 0 8px;
              font-size: 11px; font-weight: bold; letter-spacing: 0; line-height: 13px;
            }
          } 
          .title-box  { margin-top: 5px;
            span { font-size: 18px; }
          }
          .info-box   { margin-top: 15px;
            dl { padding-left: 50px;}
            dt, dd { font-size: 13px; line-height: 1.69;}
            dt { padding-right: 8px; margin-right: 8px;
              &::before { margin-top: 8px; height: 12px; }
            }
          }
          .btn-box    { margin-top: 15px;
            .btn { width: 125px; } 
          }
        }
      }

      
    }
  }

  /* 카운터 */
  &.type_panel-counter{
    .panel-header { padding: 30px 0;
      .title { font-size: 32px; font-weight: bold; color:$colorB; text-align: center; } 
    }
    .panel-body   { margin-top: 50px; }
    .panel-footer { margin-top: 70px; 
      .tac { .btn{width: 170px;} }
    }

    
    .countdown-article {
      .trp_counter-area { margin-top: 30px;
        display: flex; justify-content: center; 
        .dot { position: relative; width: 30px; height: 180px; 
          &::before , &::after {content: ""; display: block;
            position: absolute; top:50%; left: 50%;
            @include transform( translate(-50%, -50%));
            width: 6px; height: 6px; border-radius: 50%;
            background-color: #21a099;
          }
          &::before { margin-top: -6px; }
          &::after  { margin-top: 6px; }
        }
      }
    }

    .schedule-article { text-align: center;
      .schedule_name { font-size: 42px; font-weight: 600; color:$colorB; line-height: 60px; }
      .info-list { display: flex; justify-content: center;  margin-top: 20px;
        li { position: relative; font-size: 20px; font-weight: 500; 
          em { font-weight: 600; color:$colorB;}
          span { margin-left: 15px;}
        }
        li + li {  margin-left: 30px; padding-left: 30px;
          @include list-div_bar(5px, 0px, 18px, #ccc ); 
        }
      }
    }

    /* ===== countdown ==== */
    .trp_counter-area{
      /* Skeleton */
      ul.flip {
        position: relative; float: left; margin: 5px; width: 160px; height: 90px;
        font-size: 80px; font-weight: bold; line-height: 87px;
        border-radius: 6px; box-shadow: 0 2px 5px rgba(0, 0, 0, .7);
        li { z-index: 1;
          position: absolute; left: 0; top: 0;
          width: 100%; height: 100%;
          &:first-child { z-index: 2; }
          .li-box { display: block; height: 100%; perspective: 800px; 
            div { z-index: 1;
              position: absolute; left: 0;
              width: 100%; height: 50%;
              overflow: hidden;
              .shadow { position: absolute; width: 100%; height: 100%; z-index: 2; }
              &.up { transform-origin: 50% 100%; top: 0; 
                div.inn { top: 0; } 
              }
              &.up:after { content: ""; position:absolute; top:44px; left:0; z-index: 5;
                width: 100%; height: 3px; background-color: rgba(0,0,0,.4);
              }
              &.down { transform-origin: 50% 0%; bottom: 0; 
                div.inn { bottom: 0; }    
              }
              div.inn { position: absolute; left: 0; z-index: 1;
                width: 100%; height: 200%;
                color: #ccc; text-shadow: 0 1px 2px #000; text-align: center;
                background-color: #333; border-radius: 6px;
              }
            }
          }
        }
        li.on{ z-index: 4;} 
      }
      &.type-3{
        ul.flip  { width: 180px; height: 180px; box-shadow: initial; 
          margin: 0;
          li {
            div.up{
              &:after { 
                top:calc(100% - 1px); height: 1px;
                background-color: rgba(0, 0, 0, 0.1);
              }
            }
            div.down{
              div.inn {  color:#4db4af; background-color: #f9f9f9;}
            }
            div div.inn { font-size: 110px; font-weight: 600; color:#00948c;
              line-height: 180px; 
              border: 1px solid #ccc; border-radius: 0;
              text-shadow: 0 1px 1px #006666;
              background-color: #f6f6f6;
            }
          }
        }
      }

      &.play{
        ul li{
          &.before {z-index: 3;
            .up { z-index: 2; animation: turn2 .5s linear both; }
          }
          &.active { animation: asd .5s .5s linear both; z-index: 2; 
            .down { z-index: 2; animation: turn .5s .5s linear both; }
          }
        }
        ul li.before .up .shadow {
          background: -moz-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, .1)), color-stop(100%, rgba(0, 0, 0, 1)));
          background: linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -o-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -ms-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: linear-gradient(to bottom, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          animation: show .5s linear both;
        }
        ul li.active .up .shadow {
          background: -moz-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, .1)), color-stop(100%, rgba(0, 0, 0, 1)));
          background: linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -o-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: -ms-linear-gradient(top, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          background: linear-gradient(to bottom, rgba(0, 0, 0, .1) 0%, rgba(0, 0, 0, 1) 100%);
          animation: hide .5s .3s linear both;
        }
        ul li.before .down .shadow {
          background: -moz-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, .1)));
          background: linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -o-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -ms-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          animation: show .5s linear both;
        }
        ul li.active .down .shadow {
          background: -moz-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(100%, rgba(0, 0, 0, .1)));
          background: linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -o-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: -ms-linear-gradient(top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .1) 100%);
          animation: hide .5s .3s linear both;
        }
      }
    }

    /* SHADOW DOWN*/
    @keyframes asd {
      0% { z-index: 2; }
      5% { z-index: 4; }
      100% { z-index: 4; }
    }
    @keyframes turn {
      0% { transform: rotateX(90deg); }
      100% { transform: rotateX(0deg); }
    }
    @keyframes turn2 {
      0% { transform: rotateX(0deg); }
      100% { transform: rotateX(-90deg); }
    }

    @keyframes show {
      0% { opacity: 0; }
      100% { opacity: 1; }
    }
    @keyframes hide {
      0% { opacity: 1; }
      100% { opacity: 0; }
    }


  }

  /* 경매결과 - 텝 */
  &.type_panel-result_tab {
    > .panel-header { padding: 20px 0 0;}
    @media all and (max-width:$G-wMobile) {
      > .panel-header { padding: 25px 0 0;}
    }
    .search_tab-article {padding: 40px 0 30px;
      .article-body{ display: flex; justify-content: space-between; 
        .col1 { width: calc( 100% - 250px);}
        .col2 { width: 250px;}
      }
      @media all and (max-width:$G-wMobile)  {
        padding: 15px 0 30px;
        .article-body{ 
          flex-direction: column; justify-content: initial; 
          .col1, .col2 { width: 100%; }
          .col2 { margin-top: 5px;}
        }
      }
      .count-box { 
        font-size: 18px; font-weight: 600; line-height: 42px;
        span { color: $colorB;}
        em   { color: $colorP;}
        @media all and (max-width:$G-wMobile)  {
          font-size: 14px; line-height: 43px;
        }
      }
    }
  }

  /* 경매결과 - 리스트 */
  &.type_panel-product_result_list {
    position: relative;
    .product-list { display: flex; flex-wrap: wrap;
      width: calc(100% + 20px); margin-left: -10px;
      li { width: 50%; padding: 0 10px; margin-bottom: 50px;
        .li-inner { border: 1px solid #d3d3d3; }
      }
      @media all and (max-width:$G-wMobile)  {
        width: 100%; margin-left: 0px;
        li { width: 100%; padding: 0; margin-bottom: 20px; }
      }
    }
    .item-article {
      .image-area{
        .img-ratio { padding-bottom: 56.1538%; /* 56.25 */}
      }
      .typo-area { padding: 35px 40px 40px;
        .title-box { 
          .title { font-size: 26px; font-weight: 600; color:$colorB2;}
        }
        .info-box{ margin-top: 10px;
          dl { font-size: 18px; display: flex;}
          dl + dl { margin-top: 5px;}
          dt { font-weight: 600; color:$colorB2; }
          dd { font-weight: 500; margin-left: 10px; padding-left: 10px;
            position: relative; 
            @include list-div_bar( 6px , 0, 14px, #ccc)
          }
        }
        .btn-box{ margin-top: 35px;
          .btn { width: 170px;}
        }
      }
      @media all and (max-width:$G-wMobile)  {
        .typo-area { padding: 15px 20px 20px;
          .title-box { 
            .title { font-size: 18px; }
          }
          .info-box{ margin-top: 15px;
            dl { font-size: 13px; }
            dl + dl { margin-top: 5px;}
            dt { }
            dd { margin-left: 8px; padding-left: 8px;
              @include list-div_bar( 3px , 0, 12px, #ccc)
            }
          }
          .btn-box{ margin-top: 25px;
            .btn { width: 100%;}
          }
        }
      }
    }
    .panel-footer {
      .set-pc_mb { margin-top: 10px; 
        .paging-area { margin-top: 0;}
      }
      @media all and (max-width:$G-wMobile)  {
        .set-pc_mb { margin-top: 10px;
          .only-mb { text-align: center;
            .btn { width: 160px; }
          }

        }
      } 
    }
  }



}