      .num {font-weight: 700;}
      .icon-viewer { margin-right:10px; }
    }
    .btn.btn_refresh { background:#f3f3f3; border:none;
      font-size:14px; font-weight: 500; padding:16px; padding-left:30px; position:absolute; right:0; top:-5px;
      .ic_refresh {display:inline-block;@include sp_icons($icon-refresh); position:absolute; top:9px; left:12px;}
    }
  }

  .my_lot {
    .my_lot_info { position:relative;
      .count { position:absolute; top:0px; right:0; text-align: right; font-size:16px; color:#333; font-weight: 500;
