.allview_fixed-wrap{
  &.active { 
    .popup-dim { display: block;  }
    .fixed-panel{
      .panel-header { 
        button.on { i { @include rotate(180deg);} }
      }
      .panel-body   {
        height: calc(100vh - (160px));
        height: calc(var(--vh, 1vh) * 100 - (160px));
      }
    }
  }

  .popup-dim { display: none; z-index: 11;}  
  .fixed-panel{ position: fixed; bottom: 0; width: 100%; z-index: 12;
    .panel-header { padding: 15px;
      background-color: #2b2b2b; border-top-left-radius:20px;  border-top-right-radius:20px;
      text-align: center;
      span { font-size: 15px; color: #fff; font-weight: 500; line-height: 25px; }
      i { margin-top:5px; margin-left: 10px; vertical-align: top;}
    }
    .panel-body {  height: 0; overflow: hidden; @include transition(height, .37s, ease-in-out);
      .bidding-offline-left {} 

      /* 텝메뉴 */
      .lotlist-tabmenu {  padding:0px ; border-bottom:1px solid #ccc;
        .btn_item { display:inline-block; margin-right:20px;
          &:last-child {margin-right:0;}
          .lot-btn_tabmenu { position:relative;
            font-size:14px; font-weight: 500; color:$colorS; line-height: 20px; padding:10px 0; 
            &.on { color:$colorMain; font-weight: 700;
              &:after {content:''; position:absolute; z-index: 1; bottom:-1px; width:100%; height:3px; background:$colorMain; left:0; }
            }
          }
        }
      }
    

      .lotlist-wrap { background-color: #fff; padding: 0 25px;
        .lotlist-header { border-bottom:0px solid #ccc; border-bottom:none;
          .header_top  { position:relative; padding: 14px 0px 10px; margin-bottom:0px; }
          .btn_movelot { font-size:16px; color:#333; font-weight: 600; position:relative;  display:inline-block;
            &:after {content:''; position:absolute; bottom:0; left:0; width:100%; height:2px; background:#333;}
          }
          .totalcount { color:#333; font-size:13px; font-weight: 600;  line-height: 20px;
            .num {color:$colorP;}
          }
        }
        .lotlist-tabCont { 
          .mobile_scroll-type   { border-bottom:1px solid #ccc;  overflow-y: auto;
            height: calc(100vh - (160px + 85px));
            height: calc(var(--vh, 1vh) * 100 - (160px + 85px));
          }
          .lotlist-box { border-left:0px solid #ccc; border-right:0px solid #ccc;
            .lotlist-inner { 
              .lotitem { position:relative;

                .lotitem_wrap{ padding: 15px 0;
                  display: flex; align-content: center; align-items: center;
                }
               
                .view-img { position: relative; width: 45px; height: 45px; font-size: 0; line-height: 0;
                  text-align: center; background:#ededed;             
                  .img-box { position: absolute; vertical-align: middle; left: 0; right: 0; width: auto; height: 100%; text-align: center; font-size: 0; overflow: hidden;  
                    .box-inner { position: relative; display: inline-block; width: auto; height: 100%; vertical-align: middle; text-align: center; font-size: 0;
                      &::before { content: ''; height: 100%; width: 0; display: inline-block; vertical-align: middle; font-size: 0;}
                    }
                  }
                  img { display: inline-block; max-width: 100%; max-height: 100%; vertical-align: middle; height: auto; width: auto; }
               
                }
                .item-cont { position:relative; width: calc( 100% - 45px);
                  display: flex; align-content: center; align-items: center;
                  .num-box  { width: per( 45px, 530px); min-width: 30px;
                    .num { position: relative; text-align: center;
                      .snum {font-size:15px; color:$colorB2; font-weight: 600;}
                    }
                  }
                  .typo-box{ width: calc( 100% - 8.49057% - 25px );
                    .title {
                      font-size:15px; font-weight: 600; color:#333; margin-top:6px;
                    }
                    .desc { font-size:14px; font-weight: 500; color:#333; margin-top:3px;}
                    .price { font-size:14px; color:#333;  margin-top:3px; font-weight: 600;}
                  }
                  .btn-box { position: relative; width: 25px; font-size: 0; line-height: 0; margin-top: -10px;
                    .btn-lotChk { font-size:0; 
                      
                      &:after {
                        content:''; position:absolute; top:0; left:0; @include sp_icons($icon-favorite0);
                      }
                      &.on:after {
                        @include sp_icons($icon-favorite1);
                      }
                    }
                  }
                }


                &.on {
                  &:before {content:''; width:100%; background: $colorMain;  height:1px; position:absolute; top:-1px; left:0;}
                  &:after {content:''; width:100%; background: $colorMain;  height:1px; position:absolute; bottom:-1px; left:0;}
                  .lotitem_wrap{
                    &:before {content:''; width:1px; background: $colorMain;  height:100%; position:absolute; top:0px; left:-1px;}
                    &:after {content:''; width:1px; background: $colorMain;  height:100%; position:absolute; top:0px; right:-1px;}
                  }
                }

                &.live   {  
                  .num-box  .num{ 
                    &::before { content: ""; width: 6px; height: 6px; border-radius: 50%;                      
                      position: absolute; top:50%; left: 50%; margin-top: -3px; margin-left:-12px;
                      background-color: $colorSub;
                    }

                  }
                }
                &.bidded {
                  .item-cont .price {color:#db1b1b;}
                }
                &.cancel {
                  background:#f6f6f6; padding:25px 0;
                  .txt {text-align: center; font-size:15px; font-weight: 500; color:#777777;}
                }
                border-bottom:1px solid #ccc;
                &:last-child { border-bottom:none;}
              }
            }
          }

        }
      }

    }
  }
}

