.sub-wrap.type-width_list {
  .page_title-section {
    .center-box { margin-top: 100px;  padding:0 60px; max-width: 1920px;}
    @media all and (max-width:$G-width)  {
      .center-box { margin-top: 100px; padding:0 50px; max-width: 1420px; }
    }
    @media all and (max-width:$G-wMobile)  {
      .center-box {  margin-top: 30px; padding:0 25px; }
    }
  }

  .basis-section {
    &.tab-menu-section   {
      margin-top: 100px;  padding:0 60px; max-width: 1920px;
      @media all and (max-width:$G-width)  {
        margin-top: 100px; padding:0 50px; max-width: 1420px;
      }
      @media all and (max-width:$G-wMobile)  {
        margin-top: 30px; padding:0 25px; 
      }
    }
  } 


}


.basis-section{

  /* 프라이빗 세일 - 리스트*/
  &.private_sale_list-section { margin-top: 0px;  padding:0 60px; max-width: 1920px;
    @media all and (max-width:$G-width)  {
      margin-top: 100px; padding:0 50px; max-width: 1420px;
    }
    @media all and (max-width:$G-wMobile)  {
      &.private_sale_list-section { margin-top: 30px; padding:0 25px; }
    }


    .content-panel{
      &.type_panel-product_list {
      }
    }
  }


  /* P/S 가이드 */
  &.ps_guide-section { }
  @media all and (max-width:$G-wMobile)  {
    &.ps_guide-section { margin-top: 50px; }
  }
  
}


.content-panel{
 
  /* 전시 소개 */
  &.type_panel-exhibition_info { }

    // 전시소개 상세내용
    .detale_info-article { padding: 40px 0;
      .article-header { position: relative; padding-bottom: 40px; display: flex; }
      .article-body   {  padding: 40px 0; border-top: 1px solid #373737;
        .view_editor-warp { padding: 0;}
      }
      .article-footer { }
      @media all and (max-width:$G-wMobile)  {  padding: 10px 0 15px;
        .article-header { position: relative; padding-bottom: 30px; display: block; }
        .article-body   {  padding: 30px 0 25px; border-top: 1px solid #373737;}
      }


      .title-set { width: calc( 100% - 400px);
        .place { color:$colorD; }
        .title { color:$colorB; margin-top: 10px; }
        .etc-list {  margin-top: 20px; display: flex;
          li { font-weight: 600; }
          li + li {
            @include list-div_bar(5px, 20px, 12px, #d0d0d0);
          }
        }
      }
      .side_btn-set { position: relative; width: 400px;  text-align: right;
        .btn { position: absolute; bottom:0; right: 0; min-width: 160px; }
      }
      @media all and (max-width:$G-wMobile)  {
        .title-set { width: 100%;
          .title {  margin-top: 10px; }
          .etc-list {  margin-top: 10px; display: block;
            li { font-weight: 600; margin-right: 5px; display: inline-block;}
            li + li { 
              &::before { display: none;}
            }
          }
        }
        .side_btn-set { position: relative; margin-top: 15px; width: 100%;  text-align: left;
          .btn { position: relative; bottom:auto; right: auto; min-width: 160px; }
        }
      }

    }

    .line-box { padding: 25px 40px; border: 1px solid #e1e1e1;
      .info    { position: relative; font-weight: 500; color:$colorD; padding-left: 27px; 
        i    { position: absolute; left: 0; }
        span { vertical-align: middle; line-height: 22px; font-weight: 500;} 
      }
      .contact { position: relative; margin-top: 8px;  padding-left: 20vpx;
        span {font-weight: 600; color:$colorD;}
        span + span { margin-left: 20px;}
      }
      @media all and (max-width:$G-wMobile)  { padding: 25px 20px; 
        .info    {  padding-left: 20px; 
          i    { position: absolute; left: 0; top:2px;  }
          span { vertical-align: middle; line-height: 17px; display: inline; color:$colorB2; } 
        }
        .contact { position: relative; margin-top: 8px;  padding-left: 20px;
          span + span { margin-left: 0px; display: block;}
        }
      }
    }


  /* 프라이빗 세일 - 리스트 */
  &.type_panel-private_sale {
    .panel-header { padding-top: 40px;  padding-bottom: 50px; }
    .panel-body   { }
    @media all and (max-width:$G-wMobile)  {
      .panel-header { padding-top: 30px;  padding-bottom: 25px; }
    }

    .img-box { img { width: 100%;} }
    .typo-box {
      .title { color:$colorB}
      .desc  { margin-top: 20px; color:$colorB2 }
    }
    @media all and (max-width:$G-wMobile)  {
      .typo-box {
        .desc  { margin-top: 10px; }
      }
    }


    // 문의하기
    .inquiry-article  { margin-top: 70px;}
    @media all and (max-width:$G-wMobile)  {
      .inquiry-article  { margin-top: 50px;}
    }

  }



  /* P/S 가이드 */
  &.type_panel-ps_guide {
    .panel-body { }
    .panel-footer { margin-top: 100px;}
    .row_2 { display: flex; 
      .col_item { width: 50%;}
      .col_item:first-child { padding-right: 50px;}
      .col_item:last-child  { padding-left: 50px; }
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-footer { margin-top: 50px;}
      .row_2 { display: block; 
        .col_item { width: 100%;}
        .col_item:first-child { padding-right: 0px;}
        .col_item:last-child  { padding-left: 0px; }
      }
    }

    .guide-article + .guide-article { margin-top: 40px;}
    .guide-article {
      .col_item.image{ img {width: 100%; max-width: 610px;} }
      .col_item.typo { display: flex;  flex-direction: column; justify-content: center;
        .typo-header { 
          em, span { display: inline; }
          em { color:$colorP;}
          span {color:$colorB2}
        }
        .typo-body { margin-top: 10px;
          span { color:$colorD; line-height: 1.78;}
        }

      }
    }
    .img-box { img { width: 100%;} }
    @media all and (max-width:$G-wMobile)  {
      .guide-article + .guide-article { margin-top: 40px;}
      .guide-article {
        .col_item.image + .col_item.typo  { margin-top: 25px;}
        .col_item.typo  + .col_item.image { margin-top: 25px;}
        .col_item.image{ img {width: 100%; max-width: none; } }
        .col_item.typo {  display: flex;  flex-direction: column; justify-content: center;
          .typo-body { margin-top: 15px; }
        }
      }
    }

 
  }








}