/* =============================== 페이지 이름  */
.sub-wrap.type-pagemain {
  .page_title-section {
    .section-inner.full_size { height: 180px;
      .center-box { position: relative;
        width: 100%; ; max-width: 1420px; padding: 0 50px;
        margin-left: auto; margin-right: auto;
      }
    }

    .page_title { height: 180px; text-align: left;}
  }
  @media all and (max-width:$G-wMobile)  {
    .page_title-section {
      .section-inner.full_size {  height: 85px;
        .center-box { padding: 0 25px;
          margin-left: auto; margin-right: auto;
        }
      }

      .page_title { height: 85px; text-align: left;}
    }
  }
}



/* =============================== 섹션 이름  */
.basis-section{
  /* news_letter */
  &.news_letter-section { margin-top: 100px; }
  @media all and (max-width:$G-wMobile)  {
    &.news_letter-section { margin-top: 40px; }
  }
}



/* =============================== 페널이름 */
.content-panel{
  /* news_list */  
  &.type_panel-news_list {  
    .panel-footer { 
      .page-set { text-align: center;
        .btn { width: 170px;}
      }
    }
    .news-list { display: flex; flex-wrap: wrap;
      width: calc(100% + 20px); margin-left: -10px;
      li { margin-bottom: 40px; width: 25%; padding: 0 10px;}
    }

    .typo-area { padding-bottom: 5px;
      .title { margin-top: 15px;
        span, i { vertical-align: middle;}
        span { font-size: 20px; font-weight: 600; color:$colorB2; line-height: 1.5; }
        i.new { display: inline-block; margin-left: 5px;  padding: 2px 0 1px;
          width: 14px; background-color: #f23f3f; 
          font-size: 11px; color: #fff; font-weight: 500; line-height: 1; text-align: center;
        }
      }
      .desc { margin-top: 10px;
        span { font-size: 18px; font-weight: 500; color:$colorS; line-height: 1.5;}
      }
    }
    @media all and (max-width:$G-wMobile)  {
      .panel-footer { 
        .page-set { text-align: center;
          .btn { width: 160px;}
        }
      }
      .news-list { display: block; 
        width: 100%; margin-left: 0px; 
        /* 고객사 수정 [0420] */
         max-width: 350px; margin: 0 auto; margin-bottom: 40px;
        li { margin-bottom: 40px; width: 100%; padding: 0px;}
      }

      .image-area {
        .img-align { img {width: 100%;}}
      }
      .typo-area { padding-bottom: 5px;
        .title { margin-top: 10px;
          span, i { vertical-align: middle;}
          span { font-size: 14px;  }
          i.new {  margin-left: 5px;  padding: 2px 0 2px;
            width: 15px; background-color: #f23f3f; 
            font-size: 11px;
          }
        }
        .desc { margin-top: 0px;
          span { font-size: 13px;}
        }
      }

    }
  }
  
}