
.basis-section{
  /* 마이페이지 */
  &.mypage-section { margin-top: 100px;
    /* 버전별 삭제 */

    .mypage-container {
      display: flex;
      /* LNB */
      > .aside-area { width: 280px; }
      /* Content */
      > .content-area { padding-left: 40px; width: calc(100% - 280px); }
    }
    .aside-area {
      .mem-info-wrap {
        display: flex;
        justify-content: space-between;
        margin-top: 5px;
        .mem-lv-box {
          > div { display: inline-block; }
          .mem-lv {
            padding: 0px 15px;
            height: 26px;
            line-height: 26px;
            color: #fff;
            &.lv-1 { background-color: $colorL; }
            &.lv-2 { background-color: $colorP; }
          }
          .mem-period {
            color: $colorP;
            font-size: 20px;
            font-weight: 700;
            margin-left: 10px;
          }
        }
        .mem-record-box {
          position: relative;
          > a {
            display: inline-block;
            line-height: 26px;
          }
          .record-button {
            text-decoration: underline;
            font-weight: 500;
          }
          .tooltip-button {
            margin-left: 5px;
            > i { vertical-align: middle; }
          }
          .tooltip-subbox {
            display: none;
            position: absolute;
            bottom: 100%;
          }
          .tooltip-button:active + .tooltip-subbox { display: block; }
        }
      }
      .mem-button-wrap {
        margin-top: 30px;
        .btn { width: 100%;}
      }
      .mypage-lnb-wrap {
        margin-top: 45px;
        .mypage-lnb {
          ul, li { display: block; }
          li {
            line-height: 30px;
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 20px;
            position: relative;
            > a, > span {
              display: inline-block;
              padding: 5px 0
            }
            > a {
              i {
                position: absolute;
                right: 25px;
                top: 50%;
                transform: translateY(-50%);
              }
            }
            li {
              font-size: 18px;
              font-weight: 500;
              margin-bottom: 0;
              &.on {
                font-weight: 600;
                color: $colorP;
              }
            }
          }
        }
      }
    }
    .content-area {
      .search-wrap {
        height: 70px;
        border-bottom: 1px solid $colorL;
        .search-bar {
          display: flex;
          justify-content: space-between;
          margin-top: 10px;
          .date-wrap {
            .select-box { margin-left: -20px;
              select { border: 0; outline: 0; }
            }
          }
          .search-detail-btnbox {
            .search-detail-btn {
              margin-top: 5px;
              i {
                display: block;
              }
            }
          }
        }
        .search-detail-box { display: none; }
      }
    }
  }
  @media all and (max-width:$G-wMobile) {
    &.mypage-section {
      margin-top: 0;
      padding: 0;
      .mypage-container { display: block;
        > .aside-area { width: auto; }
        > .content-area { width: auto; padding: 0;
          .subtitle-wrap { padding: 40px 25px 0; }
          .tabmenu-wrap {}
          .contents-wrap { padding: 0 25px;
            .search-wrap { height: auto;
              .search-bar { margin-top: 5px; padding: 5px 0; }
            }
          }
        }
      }
    }
  }
}




.content-panel{

  /* 마이페이지 메인 */  
  &.type_panel-mypage_main {
    @media all and (max-width:$G-wMobile) {
      .mypage-container {
        > .aside-area {
          .mem-infobox-wrap { background: $colorBGw; padding: 37px 25px 25px; }
          .mypage-lnb-wrap { padding: 0 25px; margin-top: 30px; }
        }
        > .content-area { display: none;}
      }
      .aside-area {
        .mem-infobox-wrap {
          .mem-name-wrap { padding-right: 10px; float: left; }
          .mem-info-wrap { margin-top: 0;
            .tt2 { font-size: 25px; line-height: 1; }
            .mem-lv-box {
              .mem-period { margin-left: 5px; font-size: 15px; }
              .mem-lv { font-size: 13px; }
            }
            .mem-record-box a { font-size: 15px; }
          }
          &::after { display: block; content: ""; clear: both; }
        }
        .mem-button-wrap { margin-top: 25px; }
        .mypage-lnb-wrap {
          .mypage-lnb {
            li { font-size: 15px;
              li { font-size: 14px;}
              > a, > span { padding: 2px 0; }
              > a i { right: 0; }
            }
          }
        }
      }
    }
  }
  /* 응찰신청내역 */
  .bid-list {
    .bid-item {
      + .bid-item { margin-top: 30px; }
      dt {
        padding: 30px 0; border-bottom: 1px solid #ccc;
        .title-area {
          .title {
            span { @include ellipsis-line(2); }
          }
          .desc { margin-top: 5px; }
        }
      }
      dd { 
        padding: 20px; border-bottom: 1px solid #d6d6d6;
        display: flex;
        .thumb-area {
          width: 160px;
          .img-ratio { background-color: $colorBGw;}
        }
        .text-area {
          padding: 18px 0 20px 40px;
          > div {
            font-size: 18px;
            > div { display: inline-block; }
          }
          > div + div { margin-top: 10px;}
          .desc { margin-top: 5px;}
          .num { font-weight: 600;
            .m-ver { display: none;}
          }
          .titlename { font-weight: 700; font-size: 22px; }
          .year { font-weight: 600; }
          .price { font-size: 16px;
            .tit { font-weight: 600; margin-right: 15px; }
          }
          .bid_result-icon {
            height: 21px; line-height: 21px; padding: 0 16px; font-size: 14px; margin-left: 5px;
            background-color: $colorP; color: #fff;
          }
        }
      }
    }
  }
  .bid-list-wrap {
    .button-area { margin-top: 60px;
      .btn { width: 170px; }
    }
  }
  @media all and (max-width:$G-wMobile) {
    .bid-list {
      .bid-item {
        display: block;
        + .bid-item { margin-top: 25px; }
        dd {
          .thumb-area { width: 140px; }
          .text-area {
            padding: 0 0 0 25px; width: calc(100% - 140px);
            > div { font-size: 14px; }
            .titlename { font-size: 16px; }
            .price { font-size: 13px;
              .tit { font-size: 13px;}
            }
            .num .m-ver { display: inline-block; }
            .pc-ver { display: none; }
          }
        }
      }
    }
    .bid-list-wrap {
      .button-area { margin-top: 30px;
        .btn { width: 160px; }
      }
    }
  }

  /* 라이브경매 > 온라인패들응찰내역 */
  &.type_panel-mypage_auction {
    .auction-live {
      .bid-list {
        .bid-item {
          dt {
            display: block;
            .title-area {
              display: flex; justify-content: space-between; align-items: center;
              position: relative; padding-bottom: 40px;
              .desc {
                position: absolute; bottom: 0; left: 0;
              }
              .btn-area {
                padding-left: 40px;
                .btn { width: 170px; }
              }
            }
          }
          dd {
            .text-area { width: calc(100% - 340px );}
            .btn-area {
              width: 180px; padding-left: 40px;
              .btn { width: 100%;}
            }
          }
        }
      }
    }

    @media all and (max-width:$G-wMobile) {
      .aside-area { display: none;}
      .auction-live {
        .bid-list {
          .bid-item {
            dt {
              display: block;
              .title-area {
                display: block; position: static; padding-bottom: 0;
                .desc { position: static;}
                .btn-area { padding: 0; margin-top: 20px;
                  .btn { width: 160px; }
                }
              }
            }
            dd {
              flex-wrap: wrap;
              .text-area { width: calc(100% - 140px); }
              .btn-area {
                width: 100%; padding-left: 0; padding-top: 25px;
                .btn { width: 100%; }
              }
            }
          }
        }
      }
    }
  }
}








