
/* 섹션 이름  (언론보도, 채용공고, 공지사항)*/
.basis-section{
  /* 게시판 섹션 */
  &.bbs-section {
    margin-top: 100px;
    /* 공지사항 게시판 섹션 수정 */
    &.bbs-media-section {
      margin-top: 0;
      .panel-header {
        padding-top: 0; padding-bottom: 80px;
        .type-left {
          .tab-list {
            height: 75px;
            li {
              a {
                height: 75px; line-height: 75px; font-size: 20px; padding: 0;
              }
              &.active a::after {
                bottom: 0;
              }
            }
          li + li { margin-left: 60px;}
          }
        }
      }
    }
    /* 게시판 탑 */
    .bbs-top {
      display: flex;
      justify-content: space-between;
      .bbs_count-area {
        font-weight: 700;
        height: 42px;
        line-height: 42px;
        .point { color: $colorP; }
      }
      .bbs_search-area {
        .bbs_search-slctbox {
          width: 150px;
        }
        .bbs_search-box {
          display: flex;
          border: 1px solid #B2B2B2;
          input {
            width: calc(100% - 38px);
            height: 40px;
            border: 0;
          }
          input::placeholder {
            font-size: 16px;
          }
          .bbs_search-btn {
            width: 38px;
            height: 40px;
            background: url(/images/pc/icon/ic_search_black_24.svg) no-repeat center center / 21px 21px; 
            cursor: pointer; 
            padding: 0;
            font-size: 0;
          }
        }
      }
    }
    /* 게시판 테이블 */
    .table-wrap { margin-top: 25px; }
    .data-table {
      width: 100%;
      thead { display: none; }
      tr { border-bottom: 1px solid #ccc;}
      th, td { height: auto; }
      td {
        padding: 50px 30px;
        color: $colorD;
        border: 0;
      }
      .bbs-subject {
        font-size: 24px;
        text-align: left;
        font-weight: 700;
        @include ellipsis-line(2);
        .tit, span { @include ellipsis-line(2); }
      }
      .bbs-media, .bbs-date {
        font-size: 18px;
        white-space: pre-line;
      }
      .bbs-media { display: block; }
      .bbs-date { width: 180px; }
      .icon_down {
        margin-left: 10px;
        vertical-align: text-top;
      }
      /* 채용공고 게시판 테이블 */
      &.bbs-table2 {
        .bbs-date.long { width: 310px; }
        .bbs-subject {
          display: flex;
          span { vertical-align: middle;}
        }
        .icon-wrap {
          // display: inline-block;
          margin-left: 20px;
          vertical-align: middle;
          display: flex;
          align-items: center;
        }
        .mem-icon {
          // display: inline-block;
          width: 55px;
          height: 22px;
          line-height: 20px;
          font-size: 14px;
          text-align: center;
          border: 1px solid;
          &.icon-new { border-color: #3e9ccc; color: #3e9ccc;}
          &.icon-new { border-color: $colorP; color: $colorP;}
          &.icon-recruiting {
            border-color: $colorB2;
            background-color: $colorB2;
            color: #fff;
          }
        }
        .mem-icon + .mem-icon {
          margin-left: 10px;
        }
      }
      /* 공지사항 게시판 테이블 */
      &.bbs-table3 {
        .bbs-division { width: 100px; }
      }
    }
    /* 페이징 */
    .paging-area { margin-top: 50px; }
    /* 공지사항 리스트 */
    .type_panel-notice {
      .bbs-top .bbs_search-area {
        display: block;
        .bbs_search-box {
          margin-left: 0;
        }
      }
    }
    /* 상세보기 공통 */
    .view-header {
      border-top: 1px solid $colorL;
      background-color: $colorBGw;
      padding: 30px 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      > .title-area {
        .title { font-size: 24px; font-weight: 700; color: $colorB; line-height: 1.36; }
        .desc { margin-top: 0;  font-size: 18px; color: $colorS; }
      }
      > .date-area {
        font-size: 18px;
        font-weight: 700;
      }
    }
    .view-header + .file-area {
      padding: 25px 40px;
      border-bottom: 1px solid #ccc;
      font-size: 18px;
      line-height: 1.5;
      .file-item {
        > i { vertical-align: text-top; }
        > i + span { margin-left: 5px;}
      }
    }
    .view-body {
      margin-top: 60px;
      padding: 0 40px;
      .view_editor-wrap {
        font-size: 18px;
        line-height: 1.5;
        padding-bottom: 60px; }
    }
    .view-footer {
      border-top: 1px solid #ccc;
      .button-area {
        padding: 0;
        margin-top: 60px;
      }
    }
    /* 채용공고 상세보기 */
    .type_panel-incruit_view {
      .panel-header { padding: 0; }
      .incruit_view-header {
        .title-area { display: flex; flex-direction: column; justify-content: center;
        }
        .button-area {
          .btn { width: 170px; }
          .btn + .btn { margin-left: 5px; }
          .btn span + i { width: auto; }
        }
      }
    }
    /* 채용공고 지원하기 */
    .type_panel-board {
      .panel-header { padding: 0 0 15px; }
      .incruit_apply-table {
        border-top: 1px solid $colorL;
        th {
          width: 170px;
          background: none;
        }
        input { width: 43%; }
        .file-btn { width: 170px; }
        .mark_list-wrap { margin-top: 10px; }
      }
      .table-wrap.policy {
        margin-top: 80px;
        .trp.checkbox-box label { font-size: 20px; }
        .accordion-list > li{
          .header-area .accordion_name { padding: 18px 20px; }
          .con-area { display: block; }
        }
        ol.mark_num-list li { list-style: decimal; margin-left: 18px; }
        ol.mark_num-list li + li { margin-top: 1rem; }
      }
      .button-area { margin-top: 80px;
        .btn + .btn { margin-left: 5px; }
      }
    }
  }
  /* 문의하기 */
  &.bbs-section + .last-section {
    margin-top: 100px;
    .content-panel > .panel-header {
      padding: 0;
    }
  }
  
  @media all and (max-width:$G-wMobile)  {
    &.bbs-section {
      margin-top: 30px;
      /* 공지사항 게시판 섹션 수정 */
      &.bbs-media-section {
        margin-top: 0;
        .panel-header {
          padding-top: 0; padding-bottom: 25px;
          .type-left {
            height: 45px;
            .tab-list {
              height: 45px; padding: 0;
              li {
                a {
                  height: 45px; line-height: 45px; font-size: 15px; padding: 0;
                }
                &.active a::after {
                  bottom: 0;
                }
              }
            li + li { margin-left: 30px;}
            }
          }
        }
      }
      /* 게시판 탑 */
      .bbs-top {
        display: block;
        .bbs_count-area {
          width: auto;
          font-size: 14px;
        }
        .bbs_search-area {
          width: auto;
          .bbs_search-slctbox {
            select {
              font-size: 14px;
              padding-left: 10px;
            }
          }
          .bbs_search-box {
            width: auto;
            input {
              font-size: 14px;
              height: 41px;
              padding: 5px 0 5px 15px;
            }
            input::placeholder {
              font-size: 14px;
            }
            .bbs_search-btn {
              height: 41px;
            }
          }
        }
      }
      /* 게시판 테이블 */
      .bbs-table {
        th, td { height: auto; }
        tr {
          /* display: grid;
          grid-template-columns: repeat(2, 1fr); */
          display: block;
          position: relative;
          padding: 25px 0;
        }
        td { padding: 0; }
        .bbs-media {
          text-align: left;
          width: auto;
          font-size: 13px;
        }
        .bbs-date {
          width: auto;
          font-size: 14px;
          text-align: left;
          display: block;
          margin-top: 7px;
        }
        .bbs-subject {
          font-size: 17px;
          line-height: 1.3;
          display: block;
        }
        /* 언론보도 게시판 테이블 수정 */
        &.bbs-table1 {
          .bbs-subject {
            .bbs-media { font-weight: 500;}
            .tit {
              margin-top: 7px;
              font-size: 17px;
              line-height: 1.3;
            }
          }
        }
        /* 채용공고 게시판 테이블 */
        &.bbs-table2 {
          tr {
            display: flex;
            flex-direction: column-reverse;
          }
          .bbs-subject {
            order: 1;
            display: flex;
            flex-direction: column-reverse;
            padding-top: 0;
          }
          .bbs-date.long {
            text-align: left;
            margin-top: 7px;
            position: static;
            width: auto;
          }
          .icon-wrap {
            margin-left: 0;
            margin-bottom: 7px;
            display: block;
          }
          .mem-icon {
            display: inline-block;
            width: 36px;
            height: 15px;
            line-height: 13px;
            font-size: 11px;
          }
          .mem-icon + .mem-icon { margin: 0; }
        }
        /* 공지사항 게시판 테이블 */
        &.bbs-table3 {
          tr {
            display: flex;
            flex-direction: column;
          }
          .bbs-subject {
            margin-top: 10px;
            padding-right: 20px;
            position: relative;
            .icon_down {
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          .bbs-division {
            width: auto;
            text-align: left;
          }
        }
      }
      /* 페이징 */
      .paging-area {
        margin-top: 30px;
      }
      /* 상세보기 공통 */
      .view-header {
        flex-direction: column;
        padding: 25px;
        > .title-area {
          width: 100%;
          .title { font-size: 17px; }
          .desc { font-size: 14px; }
        }
        > .title-area + .date-area {
          width: 100%;
          padding-top: 5px;
          font-size: 13px;
        } 
      }
      .view-header + .file-area {
        padding: 20px 25px;
        font-size: 14px;
      }
      .view-body {
        margin-top: 25px;
        padding: 0 20px;
        .view_editor-wrap { font-size: 14px; }
      }
      .view-footer {
        .button-area { margin-top: 30px;
          .btn.btn_lg { width: 160px; }
        }
      }
      /* 채용공고 상세보기 */
      .type_panel-incruit_view {
        margin-top: 40px;
        .incruit_view-header {
          padding: 20px 25px;
          .button-area {
            width: 100%; margin-top: 25px;
            .btn { width: calc(50% - 5px); }
            .btn + .btn { margin-left: 5px; }
          }
        }
      }
      /* 채용공고 지원하기 */
      .type_panel-board {
        .incruit_apply-table {
          border-bottom: 1px solid #ccc;
          display: block;
          padding-bottom: 25px;
          tr {
            display: flex;
            flex-direction: column;
            width: 100%;
          }
          th, td {
            width: 100%;
            height: auto;
            border: 0;
            padding: 0;
          }
            th {
            padding-top: 25px;
          }
          td {
            padding-top: 12px;
          }
          input {
            width: 100%;
          }
        }
        .button-area {
          margin-top: 30px;
          .btn.btn_lg { width: calc(50% - 5px); }
          .btn.btn_lg + .btn.btn_lg { margin-left: 5px; }
        }
      }
      /* 공지사항 상세보기 */
    }
    /* 문의하기 섹션*/
    &.bbs-section + .last-section {
      margin-top: 50px;
    }
  }  
}




/* =============================== 페널이름 */
.content-panel{
  /* XXXXX */  
  &.type_panel-XXXXX { 
    @media all and (max-width:$G-wMobile)  {
    }
  }

}