
/* =============================== 섹션 이름  */
.basis-section{
   &.infoaction-section { margin-top: 100px;   
      .tb1_1{font-size: 18px; font-weight: 500; line-height: 1.78;
         li, span, em {font-size: inherit; font-weight: inherit;}
      } 
   }
   @media all and (max-width:$G-wMobile)  {
     &.infoaction-section { margin-top: 30px; 
      .tb1_1{font-size: 13px; line-height: 1.7;
         li, span, em {font-size: inherit; font-weight: inherit;}
      }
    }
   }
 }
 
 
 
 
 
 /* =============================== 경매안내 */
 .content-panel{
   &.type_panel-infoaction { 
      .sub_title{ margin-top: 40px;
         &.mark_dot-list li{ padding-left: 0.7em;}
         &.mark_dot-list li:after{ top: 13px;}
         em{ color:#333; font-weight: 700;}
      }
      .tab_move-bar{
         ul{ margin-top: 100px; display: flex; width: calc( 100% + 12px); margin-left: -6px;
            li{ width: calc( 100% / 6); padding: 0 6px;
               .li-inner{ background-color: $colorBGw; border: 1px solid #dedede; }
               a{ padding: 18px 20px; border: 1px solid transparent; 
                  .teb_txt{font-size: 14px; font-weight: 700; color: $colorP;}
                  .teb_title{font-weight: 700; color: #333;}
               }
               &:hover { 
                  .li-inner{ border: 1px solid #4dada7; }
                  a { border: 1px solid #4dada7; }
               }
            }
            
         }
      }
      .article-body{ margin-top: 55px;
         .step-area{
            .step_inner{
               .step_title{
                  .teb_txt{font-weight: 700; color: $colorS;}
                  .teb_title{color: $colorB2;}
               }
               .step_con{ margin-top: 20px;
                  .con_header{
                     ol{
                        li{ font-weight: inherit;
                           span{
                              em{ color: $colorP;}
                           }
                        }  
                     }
                     .inquiry_txt{position: relative; padding-left: 30px; margin-top: 35px;
                        i{ position: absolute; top: 6px; left: 0;}
                     }
                  }
                  .con_body{ margin-top: 35px;
                     .join_box{
                        ul{display: flex;
                           li{ border: 1px solid #dedede; padding: 60px 40px; width: calc( 100% / 2 );
                              .box_header{ text-align: center;
                                 img{width: 142px;}
                                 p{color: $colorB2; margin-top: 15px;}
                              } 
                              .box_body{ margin-top: 50px;
                                 .txt_area{ 
                                    .btn_box{ margin-top: 20px;
                                       a{ min-width: 170px;}
                                    }
                                 }
                                 .explanation_area{ margin-top: 40px;
                                    dl{
                                       dt{ position: relative;  color: $colorB2;
                                          &.exception_txt{ color: $colorP;
                                             span{padding-left: 0;}
                                          }
                                          i{ position: absolute; top: 6px;}
                                          span{ padding-left: 28px; font-weight: 700;}
                                       }
                                       dd{
                                          a{ display: inline-block; padding-left: 4px;}
                                       }
                                    }
                                    dl + dl{ margin-top: 25px;}
                                 }
                              }
                           }
                           li + li{ margin-left: 20px;}
                        }
                     }
                  }
                  
               }
            }
         }
      }



     @media all and (max-width:$G-wMobile)  {
      .sub_title{ margin-top: 15px;
         &.mark_dot-list li{ padding-left: 0.7em;}
         &.mark_dot-list li:after{ top: 9px;}
         em{ color:#333; font-weight: 700;}
      }
      .tab_move-bar{
         ul{ margin-top: 40px; flex-wrap:wrap;
            width: calc( 100% + 10px); margin-left: -5px;
            li{ width: calc( 100% / 2); padding: 0 5px 5px;
               .li-inner{}
               a{ padding: 11px 15px;  
                  .teb_txt{font-size: 12px;}
                  .teb_title{font-size: 14px;}
               }
            }
            
         }
      }
      .article-body{ margin-top: 25px;
         .step-area{
            .step_inner{
               .step_con{ margin-top: 7px;
                  .con_header{
                     .inquiry_txt{padding-left: 18px; margin-top: 20px;
                        i{ top: 4px; left: 0;}
                     }
                  }
                  .con_body{ margin-top: 15px;
                     .join_box{
                        ul{ display:block;
                           li{ padding: 25px 15px; width:100%;
                              .box_header{ 
                                 img{width: 71px;}
                                 p{margin-top: 10px;}
                              } 
                              .box_body{ margin-top: 20px;
                                 .txt_area{ 
                                    .btn_box{ margin-top: 15px;
                                       a{ min-width: 160px;}
                                    }
                                 }
                                 .explanation_area{ margin-top: 15px;
                                    dl{
                                       dt{ position: relative;  color: $colorB2;
                                         
                                         
                                          span{ padding-left: 0px; }
                                       }
                                       dd{
                                          a{ display: inline-block; padding-left: 4px;}
                                       }
                                    }
                                    dl + dl{ margin-top: 15px;}
                                 }
                              }
                           }
                           li + li{ margin-left: 0px; margin-top: 10px;}
                        }
                     }
                  }
                  
               }
            }
         }
      }
     }
   } 
 }